<template>
  <div id="background">

    <header>
      <nav id="nav-bar">

        <button v-on:click="logoclick">
          <img id="nav-logo" src="../img/logo.svg">
        </button>



        <button class="menu-trigger" v-on:click="changeHamburger" v-bind:class="{ active: isActive }">
          <span></span>
          <span></span>
          <span></span>
        </button>


        <div>
          <li><button v-on:click="changetoen">English</button></li>
          <li><button v-on:click="changetojp">日本語</button></li>
          <li><button v-on:click="changetoch">中文</button></li>
          <li><button id="aboutmebutton" v-on:click="aboutme">ABOUT ME</button></li>
        </div>
      </nav>
    </header>


    <div id="overlay" v-show="show">

      <li><button v-on:click="changetoen">English</button></li>
      <li><button v-on:click="changetojp">日本語</button></li>
      <li><button v-on:click="changetoch">中文</button></li>
      <li><button id="aboutmebutton2" v-on:click="aboutme">ABOUT ME</button></li>

    </div>





    <!-- <footer>
      <p>Copyright © 2020</p>
    </footer> -->


  </div>
</template>

<script>
  export default {
    name: 'layout',
    data() {
      return {
        isActive: false,
        show: false
      }
    },
    methods: {
      changeHamburger: function () {
        this.isActive = !this.isActive,
          this.show = !this.show,
          window.scrollTo({
            top: 0,
            behavior: "smooth"
          });
      },
      changetoen: function () {
        this.$i18n.locale = (this.$i18n.locale !== 'en') ? 'en' : 'en';
        this.show = false;
        this.isActive = false
      },
      changetojp: function () {
        this.$i18n.locale = (this.$i18n.locale !== 'jp') ? 'jp' : 'jp';
        this.show = false;
        this.isActive = false
      },
      changetoch: function () {
        this.$i18n.locale = (this.$i18n.locale !== 'ch') ? 'ch' : 'ch';
        this.show = false;
        this.isActive = false
      },
      logoclick: function () {
        this.$emit('logo_event');

        // if (this.$i18n.locale === 'jp') {
        //   window.location.href = "index.html"
        // } else if (this.$i18n.locale === 'ch') {
        //   window.location.href = "index.html";
        //   this.$i18n.locale = (this.$i18n.locale !== 'ch') ? 'ch' : 'ch';
        // } else {
        //   window.location.href = "index.html";
        //   this.$i18n.locale = (this.$i18n.locale !== 'en') ? 'en' : 'en';
        // }
      },
      aboutme: function () {
        this.$emit('aboutme_event');
        this.show = false;
        this.isActive = false;


      }

    }

  }
</script>





