<template>
  <div id="app">

    <layout v-on:aboutme_event="show_aboutme" v-on:logo_event="show_home" id="layout" />
    <jumbotron v-on:aboutme_event="show_aboutme" v-on:design_event="show_design" v-show="show" />
    <introduction v-show="show" />
    <restaurantinfo v-on:menu_event="show_menu" v-on:shop_event="show_shop" v-show="show" />
    <aboutme v-show="aboutme" />
    <design v-show="design" />
    <restaurantmenu v-show="themenu" />
    <introduction2 v-show="show" v-on:kaiten_event="show_kaiten" v-on:grill_event="show_grill" />
    <restaurantshop v-show="shop"/>
    <goodsushi v-show="goodsushi" />
    <goodfish v-show="goodfish" />
    <layout2 />

  </div>
</template>

<script>
  import layout from './components/layout.vue'
  import jumbotron from './components/jumbotron.vue'
  import introduction from './components/introduction.vue'
  import restaurantinfo from './components/restaurantinfo.vue'
  import aboutme from './components/aboutme.vue'
  import design from './components/design.vue'
  import restaurantmenu from './components/restaurantmenu.vue'
  import goodsushi from './components/goodsushi.vue'
  import goodfish from './components/goodfish.vue'
  import introduction2 from './components/introduction2'
  import restaurantshop from './components/restaurantshop'
  import Restaurantshop from './components/restaurantshop.vue'
  import layout2 from './components/layout2.vue'

  export default {
    name: 'App',
    components: {
      layout,
      jumbotron,
      introduction,
      restaurantinfo,
      aboutme,
      design,
      restaurantmenu,
      goodsushi,
      goodfish,
      introduction2,
      restaurantshop,
      Restaurantshop,
      layout2
    },
    data() {
      return {
        show: true,
        aboutme: false,
        design: false,
        themenu: false,
        shop: false,
        goodsushi: false,
        goodfish: false
      }
    },
    methods: {
      show_aboutme: function () {
        this.show = false,
          this.aboutme = true,
          this.design = false,
          this.themenu = false,
          this.shop = false,
          this.goodsushi=false,
          this.goodfish=false,
          window.scrollTo({
            top: 0
          })
      },
      show_design: function () {
        this.show = false,
          this.design = true,
          window.scrollTo({
            top: 0
          })
      },
      show_menu: function () {
        this.show = false,
          this.themenu = true,
          window.scrollTo({
            top: 0
          })
      },
      show_shop: function () {
        this.show = false,
          this.shop = true,
          window.scrollTo({
            top: 0
          })
      },
       show_kaiten: function () {
        this.show = false,
          this.goodsushi = true,
          window.scrollTo({
            top: 0
          })
      },
      show_grill: function () {
        this.show = false,
          this.goodfish = true,
          window.scrollTo({
            top: 0
          })
      },
      show_home: function(){
        this.show = true,
        this.aboutme = false,
        this.design = false,
        this.themenu = false,
        this.shop = false,
        this.goodsushi = false,
        this.goodfish = false
      }
      

    }
  }
</script>

